import axios from 'axios'
import dayjs from 'dayjs'
import { useSnackbar } from 'notistack'
import { useEffect, useState } from 'react'
import { useAuthHeader, useAuthUser } from 'react-auth-kit'
import { useParams } from 'react-router'

import LocalPoliceIcon from '@mui/icons-material/LocalPolice'
import ManageSearchIcon from '@mui/icons-material/ManageSearch'
import PersonRemoveIcon from '@mui/icons-material/PersonRemove'
import SaveAltIcon from '@mui/icons-material/SaveAlt'
import ShieldIcon from '@mui/icons-material/Shield'
import VerifiedIcon from '@mui/icons-material/Verified'
import { Box, Button, Chip, type Theme, Tooltip, Typography, colors, useMediaQuery } from '@mui/material'
import {
  DataGrid,
  GRID_CHECKBOX_SELECTION_COL_DEF,
  GridActionsCellItem,
  GridColDef,
  GridRenderCellParams,
  GridRowParams,
  GridRowSelectionModel,
  GridToolbarColumnsButton,
  GridToolbarContainer,
  GridToolbarDensitySelector,
  GridToolbarFilterButton,
  GridToolbarQuickFilter,
  GridValueFormatterParams
} from '@mui/x-data-grid'

import { MEMBER_VALUES } from 'src/data/MemberValues'
import { type ClubMember, type ClubMemberForAuth } from 'src/models/ClubMember'
import { type Semester, dateParams } from 'src/utils/date'

import DetailsModal from './DetailsModal'
import 'moment/locale/ko'

interface MemberGridProps {
  semester: Semester
  year: number
}

const MemberGrid: React.FC<MemberGridProps> = ({ semester, year }) => {
  const params = useParams()
  const authUser = useAuthUser()
  const authHeader = useAuthHeader()
  const matches = useMediaQuery((theme: Theme) => theme.breakpoints.up('sm'))
  const { enqueueSnackbar } = useSnackbar()

  const clubId = Number(params.id)
  const club = authUser()?.clubs.find((c: ClubMemberForAuth) => c.id === clubId)
  const [members, setMembers] = useState<ClubMember[]>([])
  const [selectedMembers, setSelectedMembers] = useState<ClubMember[]>([])
  const [modalMembers, setModalMembers] = useState<ClubMember[]>([])
  const [memberSelection, setMemberSelection] = useState<GridRowSelectionModel>([])

  const handleUpdatedMembers = (updatedMembers: ClubMember[]) => {
    axios
      .patch(
        `${process.env.REACT_APP_APIHOST}/club/${clubId}/member`,
        updatedMembers.map(member => ({ ...member, clubId, userId: member.id })),
        {
          params: dateParams,
          headers: { Authorization: authHeader() }
        }
      )
      .then(() => {
        const updatedIndices = updatedMembers.map(member => member.id)
        setMembers(prev =>
          prev.map(member =>
            updatedIndices.includes(member.id) ? updatedMembers[updatedIndices.indexOf(member.id)] : member
          )
        )
        setModalMembers([])
        enqueueSnackbar('변경한 내용이 성공적으로 반영되었습니다.', {
          variant: 'success'
        })
      })
      .catch(error =>
        enqueueSnackbar(error?.response?.data?.message ?? '서버에서 오류가 발생한 것 같아요. 다시 시도해주세요', {
          variant: 'error'
        })
      )
  }

  useEffect(() => {
    axios
      .get<ClubMember[]>(`${process.env.REACT_APP_APIHOST}/club/${clubId}/member`, {
        headers: { Authorization: authHeader() },
        params: dateParams
      })
      .then(({ data }) => setMembers(data))
      .catch(error =>
        enqueueSnackbar(error?.response?.data?.message ?? '서버에서 오류가 발생한 것 같아요. 다시 시도해주세요', {
          variant: 'error'
        })
      )
  }, [year, semester])

  useEffect(() => setSelectedMembers(members.filter(member => memberSelection.includes(member.id))), [memberSelection])

  const Toolbar = () => {
    const handleClick = () => setModalMembers(selectedMembers)
    const handleFire = () => {
      const updatedMembers = selectedMembers.map(member => ({ ...member, firedAt: dayjs(new Date()).toISOString() }))
      handleUpdatedMembers(updatedMembers)
    }
    const handleExport = () =>
      axios
        .get(`${process.env.REACT_APP_APIHOST}/migration/${clubId}`, {
          headers: { Authorization: authHeader() },
          responseType: 'blob'
        })
        .then(({ data }) => {
          const blob = new Blob([data])
          const fileObjectUrl = window.URL.createObjectURL(blob)

          const link = document.createElement('a')
          link.href = fileObjectUrl
          link.style.display = 'none'

          link.download = `${club.name}.xlsx`

          document.body.appendChild(link)
          link.click()
          link.remove()

          window.URL.revokeObjectURL(fileObjectUrl)
        })
        .catch(error => {
          enqueueSnackbar(error?.response?.data?.message ?? '서버에서 에러가 발생했습니다.', { variant: 'error' })
        })
    if (memberSelection.length >= 1) {
      return (
        <GridToolbarContainer>
          <Button variant="text" size="small" startIcon={<ManageSearchIcon fontSize="small" />} onClick={handleClick}>
            {memberSelection.length === 1 ? 'Inspect' : 'Modify all'}
          </Button>
          <Button variant="text" size="small" startIcon={<PersonRemoveIcon fontSize="small" />} onClick={handleFire}>
            {memberSelection.length === 1 ? 'Fire' : 'Fire all'}
          </Button>
        </GridToolbarContainer>
      )
    }
    return (
      <GridToolbarContainer>
        <GridToolbarColumnsButton />
        <GridToolbarFilterButton sx={{ '& svg': { width: '18px', height: '18px' } }} />
        <GridToolbarDensitySelector />
        <Button variant="text" size="small" startIcon={<SaveAltIcon fontSize="small" />} onClick={handleExport}>
          Export
        </Button>
        <GridToolbarQuickFilter />
      </GridToolbarContainer>
    )
  }

  const columns: GridColDef[] = [
    {
      ...GRID_CHECKBOX_SELECTION_COL_DEF,
      headerName: '선택'
    },
    {
      field: 'actions',
      type: 'actions',
      headerName: '상세',
      width: 50,
      getActions: (params: GridRowParams<ClubMember>) => [
        <Tooltip title="클릭하면 회원 정보를 더 자세하게 확인할 수 있습니다.">
          <GridActionsCellItem
            icon={<ManageSearchIcon />}
            label="자세히 보기"
            onClick={() => setModalMembers([params.row])}
          />
        </Tooltip>
      ]
    },
    {
      field: 'name',
      headerName: '이름',
      width: 140,
      renderCell: (params: GridRenderCellParams<ClubMember>) => (
        <>
          <Typography sx={{ maxWidth: '80px', overflow: 'hidden', textOverflow: 'ellipsis' }}>
            {params.value}
          </Typography>
          {params.row.isAuthenticated && (
            <Tooltip title="KLUB에서 인증 완료된 회원입니다.">
              <VerifiedIcon color="primary" fontSize="small" sx={{ marginLeft: '2px' }} />
            </Tooltip>
          )}
          {params.row.permission == 'SUPERADMIN' && (
            <Tooltip title="최고 운영진 권한을 가진 회원입니다. KLUB에서 제공하는 운영진 기능을 모두 이용할 수 있습니다.">
              <LocalPoliceIcon color="warning" fontSize="small" sx={{ marginLeft: '2px' }} />
            </Tooltip>
          )}
          {params.row.permission == 'ADMIN' && (
            <Tooltip title="일반 운영진 권한을 가진 회원입니다. 권한 변경을 제외한, KLUB에서 제공하는 운영진 기능을 모두 이용할 수 있습니다.">
              <ShieldIcon color="warning" fontSize="small" sx={{ marginLeft: '2px' }} />
            </Tooltip>
          )}
        </>
      )
    },
    { field: 'studentId', headerName: '학번', width: 110 },
    { field: 'email', headerName: '이메일', width: 180 },
    { field: 'phoneNumber', headerName: '전화번호', width: 130 },
    { field: 'department', headerName: '소속 학부/학과', width: 180 },
    { field: 'level', headerName: '회원 등급', width: 100 },
    {
      field: 'isPaused',
      headerName: '활동 상태',
      width: 100,
      renderCell: (params: GridRenderCellParams<ClubMember>) => {
        if (params.row.deletedAt) {
          return (
            <Chip
              label={MEMBER_VALUES.status[3].display}
              size="small"
              sx={{ color: colors.grey[800], bgcolor: colors.grey[50] }}
            />
          )
        }
        if (params.row.firedAt) {
          return (
            <Chip
              label={MEMBER_VALUES.status[2].display}
              size="small"
              sx={{ color: colors.red[800], bgcolor: colors.red[50] }}
            />
          )
        }
        if (params.value) {
          return (
            <Chip
              label={MEMBER_VALUES.status[1].display}
              size="small"
              sx={{ color: colors.blue[800], bgcolor: colors.blue[50] }}
            />
          )
        }
        return (
          <Chip
            label={MEMBER_VALUES.status[0].display}
            size="small"
            sx={{ color: colors.green[800], bgcolor: colors.green[50] }}
          />
        )
      }
    },
    {
      field: 'attendance',
      headerName: '출석 횟수',
      width: 100,
      renderCell: (params: GridRenderCellParams<ClubMember>) => `${params.value} / ${params.row.totalAttendance}`
    },
    {
      field: 'fee',
      headerName: '회비 납부',
      width: 90,
      valueFormatter: (params: GridValueFormatterParams<boolean>) => (params.value ? '납부함' : '미납')
    },
    { field: 'note', headerName: '비고', width: 280 }
  ]

  return (
    <>
      <Box style={{ height: matches ? 720 : 480, width: '100%' }}>
        <DataGrid
          rows={members}
          columns={columns}
          rowSelectionModel={memberSelection}
          onRowSelectionModelChange={setMemberSelection}
          slots={{ toolbar: Toolbar }}
          initialState={{
            columns: {
              columnVisibilityModel: {
                email: false,
                phoneNumber: false,
                studentId: window.innerWidth >= 600,
                level: window.innerWidth >= 600,
                isPaused: window.innerWidth >= 600,
                attendance: window.innerWidth >= 600,
                fee: window.innerWidth >= 600,
                note: window.innerWidth >= 600
              }
            }
          }}
          sx={{ bgcolor: 'white', borderRadius: matches ? '10px' : 0 }}
          autoPageSize
          checkboxSelection
          disableRowSelectionOnClick
        />
      </Box>
      <DetailsModal
        userPermission={club.permission}
        targetMembers={modalMembers}
        setTargetMembers={setModalMembers}
        handleUpdate={handleUpdatedMembers}
      />
    </>
  )
}

export default MemberGrid
